html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Raleway;
  overflow-x: hidden;
  position: relative;
  background-color: #000000;
  color: #ffffff;
}

a {
  color: #000000;
  transition: color 0.3s ease-in-out;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

strong {
  font-weight: 700;
}

canvas {
  position: fixed;
  z-index: 1;
}

main {
  position: relative;
  z-index: 5;
}

.container {
  width: 70%;
  margin: 0 auto;
  display: flex;
}

.title {
  font-weight: 700;
}

.insert {
  font-weight: 800;
  color: #edca00;
  margin-left: 0.25rem;
}

.link {
  font-size: calc(0.5rem + 0.75vw);
  line-height: calc(0.75rem + 0.75vw);
  text-align: center;
  width: 15rem;
  padding: 1.25rem 2rem;
  border: 1px solid #edca00;
  color: #edca00;
  transition: all 0.3s ease-in-out;
}

.link:active, .link:hover, .link:focus {
  cursor: pointer;
  background-color: rgba(237, 202, 0, 0.5);
  color: #ffffff;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20%;
  font-size: 2rem;
  line-height: 3rem;
  text-align: center;
  min-height: 100vh;
}

.section:nth-child(even) {
  background-color: black;
  color: #ffffff;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: none;
  padding: 1rem 1.5rem;
  border-radius: 3px;
  font-size: calc(0.25rem + 0.75vw);
  background-color: transparent;
  font-family: NewYork;
}

.btn--color {
  background-color: #FF0043;
  color: #ffffff;
}

.main-color {
  color: #FF0043;
  font-style: italic;
}

.img--wrapper {
  width: 100%;
}

.img--wrapper img {
  width: 65%;
  object-fit: cover;
}

.menu-types {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.menu-types li {
  margin: 0.25rem;
  padding: 0.25rem 0.75rem;
  border-radius: 4px;
  color: #000000;
  transition: all 0.3s ease-in-out;
}

.menu-types li:hover, .menu-types li:focus, .menu-types li:active {
  cursor: pointer;
  mix-blend-mode: difference;
  color: #ffffff;
}

.load-more {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem;
  padding: 0.75rem 0;
  font-size: 1.75rem;
  text-align: center;
}

.char {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem;
  width: 70%;
  margin: 0 auto;
  position: relative;
}

.char__title {
  grid-column: 1/3;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 0;
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 2.5rem;
}

.char-box {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.char-box--wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}

.char-box__item {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  text-align: center;
  font-size: 1.5rem;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
}

.char-box__item:hover, .char-box__item:focus, .char-box__item:active {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.25);
}

.char-box__item h3 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0.5rem 0;
}

.char-description {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 0;
}

.char-description__item {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10rem 0;
  text-align: center;
  font-size: 1.5rem;
  border: 1px solid #ffffff;
}

