@import '../helpers/var';

*, *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html,
body {
    margin: 0;
    padding: 0;
    font-family: Raleway;
    overflow-x: hidden;
    position: relative;
    background-color: $black;
    color: $white;
 }    // height: 200vh
a {
    color: $black;
    transition: color 0.3s ease-in-out;
    text-decoration: none;
    &:hover {
        color: $white; } }

strong {
    font-weight: 700; }
canvas {
    position: fixed;
    z-index: 1; }

main {
    position: relative;
    z-index: 5; }

.container {
    width: 70%;
    margin: 0 auto;
    display: flex; }

.title {
    font-weight: 700; }
.insert {
    font-weight: 800;
    color: $yellow;
    margin-left: 0.25rem; }

.link {
    font-size: calc(0.5rem + 0.75vw);
    line-height: calc(0.75rem + 0.75vw);
    text-align: center;
    width: 15rem;
    padding: 1.25rem 2rem;
    border: 1px solid $yellow;
    color: $yellow;
    transition: $transition;
    &:active,
    &:hover,
    &:focus {
        cursor: pointer;
        background-color: rgba($yellow, 0.5);
        color: $white; } }

.section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20%;
    font-size: 2rem;
    line-height: 3rem;
    text-align: center;
    min-height: 100vh;
    &:nth-child(even) {
        background-color: black;
        color: $white; } }

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: none;
    padding: 1rem 1.5rem;
    border-radius: 3px;
    font-size: calc(0.25rem + 0.75vw);
    background-color: transparent;
    font-family: NewYork;

    &--color {
        background-color: $red;
        color: $white; } }

.main-color {
    color: $red;
    font-style: italic; }

.img--wrapper {
    width: 100%;
    img {
        width: 65%;
        object-fit: cover; } }

.menu-types {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;

    li {
        margin: 0.25rem;
        padding: 0.25rem 0.75rem;
        border-radius: 4px;
        color: $black;

        transition: $transition;

        &:hover,
        &:focus,
        &:active {
            // border: 1px solid $white
            cursor: pointer;
            mix-blend-mode: difference;
            color: $white; } } }
